import OpenAI from "openai";
import {CHAT_GPT_APY_KEY} from "@/constants/index"

console.log('gpt apy key', CHAT_GPT_APY_KEY)
const openAi = new OpenAI({
    apiKey: CHAT_GPT_APY_KEY
});

const messages: any = [
  {
    role: "system",
    content: `Sei un assistente per terminale per il sistema operativo. Devi fornire all'utente comandi e istruzioni in modo sintetico, semplice e conciso`,
  },
];

export async function execGpt(input: string): Promise<void> {
  messages.push({
    role: "user",
    content: input,
  });
  const stream = await openAi.chat.completions.create({
    messages: messages,
    model: "gpt-3.5-turbo",
    stream: true,
  });
  let output = "";
  for await (const part of stream) {
    const content = part.choices[0]?.delta?.content;
    output += content;
    console.log(content)
  }
  messages.push({
    role: "assistant",
    content: output,
  });
}

